.hjs-icon {
  position: relative;
  display: inline-block;
  width: 18px;
  height: 18px;
  vertical-align: middle; }
  .hjs-icon.is-small {
    width: 16px;
    height: 16px; }
  .hjs-icon.is-large {
    width: 24px;
    height: 24px; }
  .hjs-icon.is-xlarge {
    width: 48px;
    height: 48px; }
  .hjs-icon.is-xxlarge {
    width: 96px;
    height: 96px; }
  .hjs-icon.is-xxxlarge {
    width: 192px;
    height: 192px; }
  .hjs-icon.is-flipped {
    transform: scaleX(-1); }
  .hjs-icon .hjs-icon__svg {
    vertical-align: middle;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    margin: auto;
    text-align: center; }
